import React from 'react';
import MyAppBar from './components/MyAppBar';
import Home from './components/Home';
import About from './components/About';
import User from './components/User';


import {
  BrowserRouter,
  Switch,
  Route,
  Link
} from "react-router-dom";




function App() {
 
  return (
    <BrowserRouter>
    <MyAppBar />
    <Switch>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/users">
            <User />
          </Route>
          <Route path="/">
            <Home />
          </Route>
        </Switch>

    </BrowserRouter>
 

  );
}

export default App;
