import React from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import MenuIcon from '@material-ui/icons/Menu';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PersonIcon from '@material-ui/icons/Person';
import Drawer from '@material-ui/core/Drawer';
import MyDrawer from './MyDrawer';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  cartButton: {
    marginLeft: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    display: 'block',
    textAlign: 'center',
  }, 
}));


function MyAppBar() {
  const classes = useStyles();

  const [state, setState] = React.useState({
    toggle: false,
  });

  const toggleDrawer = (open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ toggle: open });
  };


  return (
    <AppBar position="static">
      <Toolbar>
        <IconButton 
          edge="start" 
          onClick={toggleDrawer(true)}
          className={classes.menuButton} 
          color="inherit" 
          aria-label="menu"
        >
        <MenuIcon />
        </IconButton>
        <Drawer open={state.toggle} onClose={toggleDrawer(false)}>
          <MyDrawer />
        </Drawer>
        <Typography variant="h6" className={classes.title}>
          Better Life
        </Typography>
        <IconButton edge="end" className={classes.cartButton} color="inherit" aria-label="user">
        <PersonIcon />
        </IconButton>  
        <IconButton edge="end" className={classes.cartButton} color="inherit" aria-label="shopcart">
        <ShoppingCartIcon />
        </IconButton>      
        
      </Toolbar>
    </AppBar>
  );
}

export default MyAppBar;
